img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat Regular'), local('Montserrat-Regular'), url('../../fonts/Montserrat-Regular.woff2') format('woff2'), url('../../fonts/Montserrat-Regular.woff') format('woff'), url('../../fonts/Montserrat-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat Medium'), local('Montserrat-Medium'), url('../../fonts/Montserrat-Medium.woff2') format('woff2'), url('../../fonts/Montserrat-Medium.woff') format('woff'), url('../../fonts/Montserrat-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat Bold'), local('Montserrat-Bold'), url('../../fonts/Montserrat-Bold.woff2') format('woff2'), url('../../fonts/Montserrat-Bold.woff') format('woff'), url('../../fonts/Montserrat-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
}
.font-heading-1 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
}
.font-heading-2 {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
}
.font-paragraph {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
}
.page {
  height: 100%;
  background-color: #ffffff;
}
.page__body {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0;
  min-height: 100%;
  min-width: 320px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  color: #434B53;
  background-color: #ffffff;
}
.page__header {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: max-content;
  background-color: #F4BCD4;
}
.header__container {
  display: flex;
  flex-direction: column;
  width: 88%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__container {
    width: 89%;
  }
}
@media (min-width: 1024px) {
  .header__container {
    width: 91%;
  }
}
@media (min-width: 1440px) {
  .header__container {
    flex-direction: row;
    justify-content: space-between;
    width: 92%;
  }
}
.header__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  min-height: 70px;
}
@media (min-width: 768px) {
  .header__top {
    min-height: 80px;
  }
}
@media (min-width: 1024px) {
  .header__top {
    min-height: 85px;
  }
}
@media (min-width: 1440px) {
  .header__top {
    min-height: 90px;
  }
}
.header__logo {
  display: flex;
  align-items: center;
}
.logo__img {
  display: block;
  margin: auto 0;
}
@media (min-width: 1024px) {
  .logo__img {
    width: 260px;
  }
}
@media (min-width: 1440px) {
  .logo__img {
    width: 285px;
  }
}
.header__toggle-container {
  margin: auto 0;
  width: 26px;
  height: 24px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__toggle-container {
    width: 36px;
    height: 36px;
  }
}
@media (min-width: 1440px) {
  .header__toggle-container {
    display: none;
  }
}
.header__toggle {
  display: block;
  position: relative;
  top: 4px;
  width: 26px;
  height: 2px;
  margin-right: 30px;
  background-color: #ffffff;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle {
    top: 7px;
    width: 36px;
    height: 3px;
  }
}
.header__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 0px;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::before {
    top: 10px;
    width: 36px;
    height: 3px;
  }
}
.header__toggle::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 0px;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  transition: all 0.5s ease 0s;
}
@media (min-width: 768px) {
  .header__toggle::after {
    top: 20px;
    width: 36px;
    height: 3px;
  }
}
.header__toggle--closed {
  position: relative;
  left: 5px;
  top: 3px;
  background-color: #F4BCD4;
}
.header__toggle--closed::before,
.header__toggle--closed::after {
  content: "";
  position: absolute;
  top: 8px;
  left: -2px;
  width: 26px;
  height: 2px;
  background-color: #ffffff;
  transform: matrix(0.71, -0.73, 0.69, 0.71, 0, 0);
}
@media (min-width: 768px) {
  .header__toggle--closed::before,
  .header__toggle--closed::after {
    top: 14px;
    left: -2px;
    width: 36px;
    height: 3px;
    background-color: #ffffff;
  }
}
.header__toggle--closed::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__toggle--closed::after {
  transform: rotate(-45deg);
}
.header__nav-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #F4BCD4;
}
@media (min-width: 1024px) {
  .header__nav-list {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .header__nav-list {
    width: 200%;
    max-width: 1250px;
    align-items: center;
  }
}
@media (max-width: 1023px) {
  .header__nav-list--closed {
    display: none;
  }
}
@media (min-width: 1024px) and (max-width: 1439px) {
  .header__nav-list--closed {
    display: none;
  }
}
@media (min-width: 1440px) {
  .header__nav-list--closed {
    display: flex;
  }
}
.header__nav-item {
  border-top: 1px solid #ffffff;
}
@media (min-width: 1024px) {
  .header__nav-item {
    border-top: none;
  }
}
@media (min-width: 1024px) {
  .header__nav-item:not(:nth-child(1)) {
    margin-left: 1em;
  }
}
.nav-item__link {
  display: block;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1em;
  line-height: 1em;
  color: #ffffff;
  padding: 1em;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .nav-item__link {
    font-size: 1.2em;
    line-height: 1.2em;
  }
}
@media (min-width: 1024px) {
  .nav-item__link {
    font-size: 1.1em;
    line-height: 1.1em;
    padding: 0.7em 0 1em 0;
  }
}
@media (min-width: 1440px) {
  .nav-item__link {
    padding: 1em 0;
  }
}
.nav-item__link:hover {
  color: #598FFC;
  transform: scale(1.1);
  transition: all 0.5s;
}
.nav-item__link:active {
  color: #ffffff;
  transform: scale(0.9);
  transition: all 0.1s;
}
